import { Link } from "react-router-dom";

const Footer = () => {
    return (
        <div className='footer-container flex'>
            <div className='footer-links flex'>
                <div className='footer-nav flex'>
                    <div className='footer-details-title'>Navigation</div>
                    <Link to='/'>Home</Link>
                    <Link to='/random'>Random</Link>
                </div>
                <div className='footer-details flex'>
                    <div className='footer-details-title'>External</div>
                    <a href='https://github.com/iMeece'>Github</a>
                    <a href='https://www.fishwatch.gov/'>Fishwatch</a>
                </div>
            </div>
            <div className='footer-copyright flex'>
                Copyright iMeece
            </div>

        </div>
    );
}
 
export default Footer;