import { useParams } from "react-router";
import useFetch from "./useFetch";
import { withRouter } from "react-router";


const Search = (props) => {

    const { name } = useParams();
    const { data, isPending, error } = useFetch('https://raw.githubusercontent.com/iMeece/fishpedia-data/main/db.json');
    let noResult = null;

    function checkIfMatch(fishName, index) {
        if (fishName.toLowerCase().includes(name.toLowerCase())) {
            noResult = false;
            return true
        };
        if (index === 114 && noResult === null) {
            noResult = true;
        }
    }

    function redirectToLink(index) {
        props.history.push('/fish/' + index);
    }

    return (
        <div>
            { error && <div>An Error has occurred</div> }
            { isPending && <div>Loading Data...</div> }
            { 
                data && 
                <div className='fish-search-container flex'>
                    { data.map((fish, index) => (
                            checkIfMatch(fish['Species Name'], index) && <div className='search-item flex' key={'search-' + index}>
                                <h5 className='search-item-title' onClick={() => redirectToLink(index)}>{fish['Species Name']}</h5>
                                { Array.isArray(fish['Image Gallery']) && <img onClick={() => redirectToLink(index)} src={fish['Image Gallery'][0].src} alt={fish['Image Gallery'][0].alt}/>}
                                { !Array.isArray(fish['Image Gallery']) && fish['Image Gallery'] !== null && <img onClick={() => redirectToLink(index)} src={fish['Image Gallery'].src} alt={fish['Image Gallery'].alt}/>}
                            </div>
                        ))
                    }
                    { noResult && <div className='search-no-results'>No Results Found</div> }
                </div>
            }
        </div>
    );
}
 
export default withRouter(Search);