import { useState } from "react";
import NavLink from "./NavLink";
import { withRouter } from "react-router";

const Nav = (props) => {

    const [searchActive, setSearchActive] = useState(false);

    function switchSearchActive() {
        setSearchActive(!searchActive);
    }

    function handleSearchEvent(e, textbox) {
        if (e.keyCode === 13) {
            switchSearchActive();
            if (textbox !== '') {
                props.history.push('/search-results/' + textbox);
            }
        };
    }

    return (
        <div className='nav flex'>
            <div className='flex'>
                <NavLink link={'/'} name={'Home'}/>
                <NavLink link={'/random'} name={'Random'}/>
            </div>
            <div>
                {!searchActive && <div className='link-searchglass' onClick={switchSearchActive}><i className='fa fa-search'></i></div>}
                {searchActive && <input className='nav-searchbar' type='text' onKeyUp={(e) => handleSearchEvent(e, document.getElementsByClassName('nav-searchbar')[0].value)} />}
            </div>
        </div>
    );
}
 
export default withRouter(Nav);