import { Redirect, withRouter } from "react-router";

const Random = () => {
    
    function generateRandomCatchId() {
        return Math.floor(Math.random() * 115);
    }

    return (
        <div>
            <Redirect to={'/fish/' + generateRandomCatchId()} />
        </div>
    );
}
 
export default withRouter(Random);