const FishInfoImages = (props) => {
    return (
        <div className='info-images info-item'>
            <h3>Image Gallery</h3>
            <div className='info-images-list flex'>
                { Array.isArray(props.gallery) && 
                    props.gallery.map((image, index) => (
                        <a href={props.gallery[index].src} key={ props.fishId + '-' + index }><img src={props.gallery[index].src} alt={props.gallery[index].alt}/></a>
                    ))
                }
                { !Array.isArray(props.gallery) && props.gallery !== null && <a href={props.gallery.src}><img src={props.gallery.src} alt={props.gallery.alt}/></a>}
            </div>
        </div>
    );
}

export default FishInfoImages;